*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.facts_sec{
    @media all and (max-width:992px){
        margin-left: 0;
    }
    background: url('../../assets/chaqmoq.jpg');
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
    margin-left: 22%;
    padding: 50px 10px;
    .this_section{
        padding: 10px 0;
        font-size: 25px;
        h1{
            color: #2696d6;
            &::after{
                content: "";
                display: block;
                height: 2px;
                background-color: #2696d6;
                width: 50px;
                margin-top: 15px;
            }
        }
    }

    .parent_facts{
        @media all and (max-width:992px){
            display: block;
        }
        display: flex;
        justify-content: space-around;
        margin: 40px 0;
        svg{
            color: #149DDD;
            font-size: 65px;
        }
        div{
            @media all and (max-width:992px){
                margin: 20px 0 0 80px;
            }
            display: flex;
            div{
                display: block;
                margin-left: 15px;
                h1{
                    color: #fff;
                    font-size: 60px;
                }
                h4{
                    color: #1a628b;
                }
            }
        }
        .projects{
            display: flex;
        }
        .support_hours{
            display: flex;
        }
        .partners{
            display: flex;
        }
    }
}