// FONTS
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@500&display=swap');
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.navigation{
    .forOpen{
        @media all and (max-width:992px){
            display: flex !important;
            
        }
        display: none !important;
        z-index: 10;
        transition: 0.4 ease;        
        position: fixed;
        display: flex;
        top: 5%;
        right: 5%;
        align-items: center;
        justify-content: center;
        width: 50px;
        height: 50px;
        border-radius: 50%;
        color: #fff;
        background-color: #149DDD;
    }
    .forClose{
        @media all and (max-width:992px){
            display: flex !important;
            color: #a8a9ac;
        }
        display: none !important;
        z-index: -10;
        position: fixed;
        display: flex;
        top: 5%;
        right: 5%;
        align-items: center;
        justify-content: center;
        width: 50px;
        height: 50px;
        border-radius: 50%;
        color: #fff;
        background-color: #149DDD;

    }

    .fixed-left-navbar{
        @media all and (max-width:992px){
            width: 70%;
            left: -100%;
            height: 100%;
        }
        margin-right: 5px;
        transition: 0.5s ease;
        position: fixed;
        z-index: 10;
        left: 0;
        width: 22%;
        background-color: #040B14;
        height: 100%;
        .main-container{
            padding: 15px 40px;
            .hero-img{
                display: flex;
                justify-content: center;
                margin: 0 auto;
                border: #2C2F3F 7px solid;
                border-radius: 50%;
                width: 130px;
                height: 130px;
                img{
                    border-radius: 50%;
                }
            }
            .hero-name{
                font-family: 'Noto Sans JP', sans-serif;
                text-align: center;
                color: #ffffff;
                padding: 15px 0;
            }
            .socials{
                justify-content: space-around;
                display: flex;
                .twitter{
                    @media all and (max-width:992px){
                        display: none;
                    }
                }
                .linkedin{
                    @media all and (max-width:992px){
                        display: none;
                    }
                }
                .telegram:hover{
                    svg{
                        color: rgb(80, 177, 223);
                    }
                }
                .facebook:hover{
                    svg{
                        color: rgb(6, 86, 133);
                    }
                }
                .instagram:hover{
                    svg{
                        color: rgb(255, 0, 76);
                    }
                }
                .twitter:hover{
                    svg{
                        color: rgb(18, 146, 163);
                    }
                }
                .linkedin:hover{
                    svg{
                        color: cadetblue;
                    }
                }
                div{
                    cursor: pointer;
                    background-color: #212431;
                    border-radius: 50%;
                    margin: 0 3px;
                    a{
                        justify-content: center;
                        align-items: center;
                        color: #fff;
                        display: flex;
                        padding: 7px 7px;
                        svg{
                            transition: 0.5s ease;
                        }
                        }
                    
                    
                }
            }
            .quicklinks{
                margin-top: 20px;
                ul{
                    li{
                        list-style-type: none;
                        .active{
                            svg{
                                color: #09719e;
                            }
                            color: rgba(255, 255, 255, 0.863);
                        }
                        &:hover{
                            color: #fff;
                            svg{
                                color: #09719e;
                            }
                        }
                        a{
                        padding: 15px 0;
                        display: flex;
                        text-decoration: none;
                        align-items: center;
                            color: #808080;
                            svg{
                                margin-right: 8px;
                            }
                    }
                    }
                }
            }
            .tel_num{
                @media all and (max-width:992px){
                    margin-top: 100px;
                }
                margin-top: 150px;
                text-align: center;
                p{
                    color: #fff;
                    span{
                        color: rgba(0, 204, 255, 0.678);
                    }
                }
            }
        }
    }
}
