// FONTS
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@300&family=Ubuntu&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Baloo+Tammudu+2:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.about_sec{
    @media all and (max-width:992px){
        margin-left: 0;
    }
    background-color: #F5F8FD;
    margin-left: 22%;
    padding: 50px 10px;
    
    .some_text{
        padding-bottom: 20px;
        p{
            font-family: 'Baloo Tammudu 2', cursive;
            font-size: 18px;
        }
    }
    .resume{
        @media all and (max-width:992px){
            display: block;
        }
        display: flex;
        .hero-image{
            img{
                width: 350px;
            }
        }
        .resume_about{
            padding-left: 20px;
            h2{
                font-family: 'Poppins', sans-serif;
                color: #173B6C;
            }
            p{
                font-size: 22px;
                font-family: 'Roboto', sans-serif;
            }
            .parent_boxes{
                font-family: 'Roboto Slab', serif;
                display: flex;
                @media all and (max-width:992px){
                    display: block;
                }
                .left_box{
                    div{
                        margin: 30px 0;
                        display: flex;
                        align-items: center;
                        svg{
                            color: #149DDD;
                        }
                        p{
                            font-size: 18px;
                            font-weight: 600;
                        }
                        span{
                            margin-left: 10px;
                            font-size: 18px;
                        }
                    }
                }
                .right_box{
                    .email{
                        @media all and (max-width:992px){
                            display: none;
                        }
                        span{
                            color: #149DDD;
                        }
                    }
                    div{
                        svg{
                            color: #149DDD;
                        }
                        span{
                            margin-left: 10px;
                            font-size: 18px;
                        }
                        margin: 30px 0;
                        display: flex;
                        align-items: center;
                        margin-left: 20px;
                        p{
                            font-size: 18px;
                            font-weight: 600;
                        }
                    }
                }
            }
    }
  }
}