// FONTS
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+JP&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


.skills_sec{
    @media all and (max-width:992px){
        margin-left: 0;
    }
    margin-left: 22%;
    padding: 50px 10px;
    background-color: #F5F8FD;
    .skills{
        @media all and (max-width:992px){
            display: block;
        }
        font-family: 'Noto Sans JP', sans-serif;
        margin: 50px 0;
        display: grid;
        grid-template-columns: 50% 50%;
        .skill_name{
            display: flex;
            h5{
                &:nth-child(2){
                    margin-left: auto;
                }
            }
        }
        .left_box{
            @media all and (max-width:992px){
                margin-right: 0;
            }
            margin-right: 10px;
            .skill1{
                .parent1{
                    height: 10px;
                    width: 100%;
                    margin: 10px 0 20px 0;
                    background-color: #DCE8F8;
                    .child1{
                        background-color: #149DDD;
                        height: 10px;
                        width: 80%;
                    }
                }
            }
            .skill2{
                .parent2{
                    height: 10px;
                    width: 100%;
                    margin: 10px 0 20px 0;
                    background-color: #DCE8F8;
