                        height: 10px;
                        width: 60%;
                    }
                }

            }
        }
        .right_box{
            @media all and (max-width:992px){
                margin-left: 0;
            }
                margin-left: 10px;
                .skill4{
                    .parent4{
                        height: 10px;
                        width: 100%;
                        margin: 10px 0 20px 0;
                        background-color: #DCE8F8;
                        .child4{
                            background-color: #149DDD;
                            height: 10px;
                            width: 90%;
                        }
                    }
                }
                .skill5{
                    .parent5{
                        height: 10px;
