//FONTS
@import url('https://fonts.googleapis.com/css2?family=Mukta:wght@300&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


.contacts_sec{
    @media all and (max-width:992px){
        margin-left: 0;
    }
    margin-left: 22%;
    padding: 50px 10px;
    .contacts{
        @media all and (max-width:992px){
            display: block;
        }
        font-family: 'Mukta', sans-serif;
        margin: 50px 0;
        display: flex;
        .left_box{
            border-radius: 15px;
            padding: 20px 10px;
            background: #fff;
            box-shadow: 0 0 24px 0 rgba(0, 0, 0, 0.185);
            .icon{
                transition: 0.4s ease;
                display: flex;
                align-items: center;
                justify-content: center;
                width: 50px;
                height: 50px;
                border-radius: 50%;
                background-color: #DFF3FC;
                svg{
                    color: #149DDD;
                }
            }
            .parent_div{
                display: flex;
                margin: 30px 0;
                &:hover .icon{
                    background-color: #149DDD;
                    svg{
                        color: #fff;
                    }
                }
                div{
                    &:nth-child(2){
                        
                        margin-left: 10px;
                    }
                }
            }
            .map{
                iframe{
                    @media all and (max-width:992px){
                        width: 90%;
                    }
                    border: none;
                    border-radius: 15px;
                }
            }
        }
        .right_box{
            @media all and (max-width:992px){
                margin-left: 0;
                margin-top: 20px;
            }
            padding-top: 30px;
            border-radius: 15px;
            margin-left: 15px;
            padding: 30px;
            background: #fff;
            width: 100%;
            box-shadow: 0 0 24px 0 rgba(0, 0, 0, 0.185);
            .first_inp{
                @media all and (max-width:992px){
                    display: block;
                }
                display: grid;
                grid-template-columns: 50% 50%;
                .name{
                    input{
                        padding: 15px;
                        width: 220px;
                        border: 1px solid rgb(189, 189, 189);
                        &:focus{
                            outline: none;
                            border: 1px solid #149DDD;
                        }
                    }
                    p{
                        font-size: 23px;
                    }
                }
            }
            .third_inp{
                margin-top: 15px;
                input{
                    padding: 15px;
                    width: 91%;
                    border: 1px solid rgb(189, 189, 189);
                    &:focus{
                        outline: none;
                        border: 1px solid #149DDD;
                    }
                }
                p{
                    font-size: 23px;

                }
            }
            .fourth_inp{
                margin-top: 25px;
                textarea{
                    border: 1px solid rgb(189, 189, 189);
                    width: 91%;
                    height: 300px;
                    &:focus{
                        outline:none;
                        border: 1px solid #149DDD;
                        padding: 5px;
                    }
                }
                p{
                    font-size: 23px;
                }
            }
            button{
                display: block;
                margin: 0 auto;
                padding: 10px 30px;
                background-color: #149DDD;
                border: none;
                border-radius: 10px;
                color: #FFFFFF;
                cursor: pointer;
            }
        }
    }
}