*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


.projects_sec{
    @media all and (max-width:992px){
        margin-left: 0;
    }
   background-color: #fff;
    margin-left: 22%;
    padding: 50px 10px;
    .projects{
        @media all and (max-width:992px){
            display: block;
            margin: 0 auto;
            .project1{
                margin-bottom: 50px;
                width: 340px !important;
                .about_pro{
                    width: 340px !important;
                    height: 220px !important;
                }

            }
            .project2{
                width: 340px !important;
                .about_pro{
                    width: 340px !important;
                }
            }
        }
        margin: 50px 0;
        display: flex;
        justify-content: space-around;
        .project1{
            width: 380px;
            height: 220px;
            background: url('../../assets/wellsoft.webp');
            background-size: cover;
            &:hover .about_pro{
                opacity: 1;
            }
            a{
                color: #fff;
                text-decoration: none;
            }
            .about_pro{
                opacity: 0;
                transition: 0.5s ease;
                padding: 15px;
                height: 220px;
                background-color: rgba(0, 0, 0, 0.575);
                color: #fff;
                font-size: 20px;
            }
        }
        .project2{
            width: 380px;
            height: 220px;
            background: url('../../assets/online__shop.png');
            background-size: cover;
            &:hover .about_pro{
                opacity: 1;
            }
            .about_pro{
                a{
                    color: #fff;
                    text-decoration: none;
                }
                opacity: 0;
                transition: 0.5s ease;
                padding: 15px;
                height: 220px;
                background-color: rgba(0, 0, 0, 0.575);
                color: #fff;
                font-size: 20px;
            }

        }
    }
}