// FONTS
@import url('https://fonts.googleapis.com/css2?family=Baloo+Tammudu+2:wght@500&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.homesec{
    font-family: 'Baloo Tammudu 2', cursive;
    width: 100%;
    height: 100vh;
    background: url('../../assets/bgprogrammer.jpg');
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    video{
        background-attachment: fixed;
    }
    .main-container{
        @media all and (max-width:992px){
            left: 20%;
        }
        position: absolute;
        left: 40%;
        top: 25%;
        .typical-text{
            color: #fff;
            h1{
                @media all and (max-width:992px){
                    font-size: 40px;
                }
                font-size: 50px;
            }
        }
        .wrap1{
            display: flex;
            align-items: center;
            div{
                h1{
                    @media all and (max-width:992px){
                        font-size: 20px !important;
                    }
                    color: #fff;
                    margin-bottom: 5px;
                }
                &:nth-child(2){
                    margin-left: 5px;
                    h1{
                        padding-top: 5px;
                        border-bottom: 3px solid rgb(4, 226, 215);
                    }
                }
            }
        }
    }
}