

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


.to_up{
    display: none;
    transition: 0.5s ease !important;
    justify-content: center;
    align-items: center;
    background-color: transparent;
    border: 1px solid red;
    width: 50px;
    height: 50px;
    position: fixed;
    top: 90%;
    right: 3%;
    border-radius: 50%;
    a{
        color: #e91912;
    }    
}